import {Script, HArg} from './CmnInterface';
import {RubySpliter} from './RubySpliter';

export const	REG_TAG	= /(?<name>[^\s;\]]+)/;	// testexport
export function	tagToken2Name_Args(token: string): [name: string, args: string] {
	const e = REG_TAG.exec(token.slice(1, -1));
	const g = e?.groups;
	if (! g) throw `${token}()`;

	const nm = g.name;
	return [nm, token.slice(1 +nm.length, -1)];
}
export function	tagToken2Name(token: string): string {
	const e = REG_TAG.exec(token.slice(1));
	const g = e?.groups;
	if (! g) throw `${token}()`;

	return g.name;
}

export function	splitAmpersand(token: string): {
		name: string;
		text: string;
		cast: string | undefined;
} {	// public
	const equa = token.replaceAll('==', '').replaceAll('!=', '').split('=');
		// != 
	const cnt_equa = equa.length;
	if (cnt_equa < 2 || cnt_equa > 3) throw '&=';
	if (equa[1].charAt(0) === '&') throw '&&';
	return {
		name: equa[0].replaceAll('', '==').replaceAll('', '!='),
		text: equa[1].replaceAll('', '==').replaceAll('', '!='),
		cast: ((cnt_equa === 3) ?equa[2].trim() :undefined)
	};
}

export class Grammar {
	constructor() {this.setEscape('');}

	REG_TOKEN	: RegExp;
	setEscape(ce: string) {
		if (this.#hC2M && (ce in this.#hC2M)) throw '[] char'+ ce +'';

		// 1059 match 20343 step (8.4ms) https://regex101.com/r/AiBDkJ/1
		const ces = ce ?? '\\';
		this.REG_TOKEN = new RegExp(
		(ce	?`\\${ce}\\S|` :'')+	// 
		'\\n+'+				// 
		'|\\t+'+			// 
		`|\\[let_ml\\s+[^\\]]+\\]`+
			`.+?`+		// [let_ml][endlet_ml]
		`(?=\\[endlet_ml[\\]\\s])`+
		`|\\[(?:[^"'#;\\]]+|`+ (ces		// 
			? `(?:"(?:\\${ces}["'#\\n]|[^"])*"|`+	// 
				`'(?:\\${ces}["'#\\n]|[^'])*'|`+
				`\\#(?:\\${ces}["'#\\n]|[^#])*\\#)`
			: `(["'#]).*?\\1`) +`|;[^\\n]*)*?]`+
		'|;[^\\n]*'+		// 
		'|&[^&\\n]+&'+		// 
		'|&&?[^;\\n\\t&]+'+	// 
		'|^\\*\\w+'+		// 
		`|[^\\n\\t\\[;${ce ?`\\${ce}` :''}]+`,	// 
		'gs');
		RubySpliter.setEscape(ce);
		this.#REG_CANTC2M = new RegExp(`[\\w\\s;[\\]*=&${ce ?`\\${ce}` :''}]`);
		this.REG_TOKEN_NOTXT = new RegExp(`[\\n\\t;\\[*&${ce ?`\\${ce}` :''}]`);
	}
	matchToken(txt: string) {return txt.match(this.REG_TOKEN) ?? []}


	// 
	bracket2macro(hArg: HArg, script: Script, idxToken: number) {
		const {name, text} = hArg;
		if (! name) throw '[bracket2macro] name';
		if (! text) throw '[bracket2macro] text';
		if (text.length !== 2) throw '[bracket2macro] text';

		this.#hC2M ??= {};
		const op = text.charAt(0);
		const cl = text.charAt(1);
		if (op in this.#hC2M) throw '[bracket2macro] text'+ op +'';
		if (cl in this.#hC2M) throw '[bracket2macro] text'+ cl +'';
		if (this.#REG_CANTC2M.test(op)) throw '[bracket2macro] text'+ op +'';
		if (this.#REG_CANTC2M.test(cl)) throw '[bracket2macro] text'+ cl +'';

		this.#hC2M[cl] = '0';	// 
		this.#hC2M[op] = `[${name} text=`;

		this.addC2M(`\\${op}[^\\${cl}]*\\${cl}`, `\\${op}\\${cl}`);

		this.replaceScr_C2M_And_let_ml(script, idxToken);
	}
	// 
	char2macro(hArg: HArg, hTag: HArg, script: Script, idxToken: number) {
		const {char, name} = hArg;
		if (! char) throw '[char2macro] char';
		this.#hC2M ??= {};
		if (char in this.#hC2M) throw '[char2macro] char'+ char +'';
		if (this.#REG_CANTC2M.test(char)) throw '[char2macro] char'+ char +'';

		if (! name) throw '[char2macro] name';
		if (! (name in hTag)) throw `[char2macro] [${name}]`;

		this.#hC2M[char] = `[${name}]`;

		this.addC2M(`\\${char}`, `\\${char}`);

		this.replaceScr_C2M_And_let_ml(script, idxToken);
	}
	#REG_CANTC2M		: RegExp;
	#REGC2M			= new RegExp('');
	#regStrC2M		= '';
	#regStrC2M4not	= '';
	addC2M(a: string, b: string) {
		this.#regStrC2M += `${a}|`;
		this.#regStrC2M4not += `${b}`;
		this.#REGC2M = new RegExp(
			`(${this.#regStrC2M}[^${this.#regStrC2M4not}]+)`, 'g');
	}

	#hC2M	: {[char: string]: string};
	REG_TOKEN_NOTXT	: RegExp;	// public
	replaceScr_C2M_And_let_ml = (scr: Script, start_idx = 0)=> {
		if (! this.#hC2M) return;

		for (let i=scr.len- 1; i >= start_idx; --i) {
			const token = scr.aToken[i];
			if (this.REG_TOKEN_NOTXT.test(token.charAt(0))) continue;

			const lnum = scr.aLNum[i];
			const a = token.match(this.#REGC2M);
			if (! a) continue;
			let del = 1;
			for (let j=a.length -1; j>=0; --j) {
				let ch = a[j];
				const macro = this.#hC2M[ch.charAt(0)];
				if (macro) {
					ch = macro +((macro.slice(-1) === ']')
						? ''
						: (`'${ch.slice(1, -1)}']`));
					// ''
				}
				scr.aToken.splice(i, del, ch);

				scr.aLNum.splice(i, del, lnum);
				del = 0;
			}
		}
		scr.len = scr.aToken.length;
	}

}
